import bwipjs from "bwip-js";
import { BarCodeType } from "./type";

export const validateBarcodeInput = (type: BarCodeType, input: string) => {
  if (!input) return false;
  if (type === "qrcode") {
    // æ¼¢å­—ã‚’å«ã¾ãªã„500æ–‡å­—ä»¥ä¸‹
    const regexp =
      /([\u{3005}\u{3007}\u{303b}\u{3400}-\u{9FFF}\u{F900}-\u{FAFF}\u{20000}-\u{2FFFF}][\u{E0100}-\u{E01EF}\u{FE00}-\u{FE02}]?)/mu;
    return !regexp.test(input) && input.length < 500;
  } else if (type === "japanpost") {
    // éƒµä¾¿ç•ªå·ã¯æ•°å­—(0-9)ã®ã¿ã€‚ä½æ‰€è¡¨ç¤ºç•ªå·ã¯è‹±æ•°å­—(0-9,A-Z)ã¨ãƒã‚¤ãƒ•ãƒ³(-)ãŒä½¿ç”¨å¯èƒ½ã§ã™ã€‚
    const regexp = /^(\d{7})(\d|[A-Z]|-)+$/;
    return regexp.test(input);
  } else if (type === "ean13") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å€¤(0-9)ã®ã¿ã€‚ãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã¾ãªã„12æ¡orãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã‚€13æ¡ã€‚
    const regexp = /^\d{12}$|^\d{13}$/;
    return regexp.test(input);
  } else if (type === "ean8") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å€¤(0-9)ã®ã¿ã€‚ãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã¾ãªã„7æ¡orãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã‚€8æ¡ã€‚
    const regexp = /^\d{7}$|^\d{8}$/;
    return regexp.test(input);
  } else if (type === "code39") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å­—(0-9)ã€‚ã‚¢ãƒ«ãƒ•ã‚¡ãƒ™ãƒƒãƒˆå¤§æ–‡å­—(A-Z)ã€è¨˜å·(-.$/+%)ã€åŠè§’ã‚¹ãƒšãƒ¼ã‚¹ã€‚
    const regexp = /^(\d|[A-Z]|\-|\.|\$|\/|\+|\%|\s)+$/;
    return regexp.test(input);
  } else if (type === "code128") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ¼¢å­—ã€ã²ã‚‰ãŒãªã€ã‚«ã‚¿ã‚«ãƒŠä»¥å¤–ã€‚
    // https://qiita.com/graminume/items/2ac8dd9c32277fa9da64
    return input.match(
      /([\u30a0-\u30ff\u3040-\u309f\u3005-\u3006\u30e0-\u9fcf]|[ï¼¡-ï¼ºï½-ï½šï¼-ï¼™ï¼ï¼‚ï¼ƒï¼„ï¼…ï¼†ï¼‡ï¼ˆï¼‰ï¼Šï¼‹ï¼Œï¼ï¼Žï¼ï¼šï¼›ï¼œï¼ï¼žï¼Ÿï¼ ï¼»ï¼¼ï¼½ï¼¾ï¼¿ï½€ï½›ï½œï½ã€œ])+/
    )
      ? false
      : true;
  } else if (type === "nw7") {
    // æœ‰åŠ¹æ–‡å­—ã¯NW-7ã¯æ•°å­—(0-9)ã¨è¨˜å·(-.$:/+)ã€‚
    // ã‚¹ã‚¿ãƒ¼ãƒˆã‚³ãƒ¼ãƒ‰ï¼ã‚¹ãƒˆãƒƒãƒ—ã‚³ãƒ¼ãƒ‰ã¨ã—ã¦ã€ã‚³ãƒ¼ãƒ‰ã®å§‹ã¾ã‚Šã¨çµ‚ã‚ã‚Šã¯ã‚¢ãƒ«ãƒ•ã‚¡ãƒ™ãƒƒãƒˆ(A-D)ã®ã„ãšã‚Œã‹ã‚’ä½¿ç”¨ã—ã¦ãã ã•ã„ã€‚
    const regexp = /^[A-Da-d]([0-9\-\.\$\:\/\+])+[A-Da-d]$/;
    return regexp.test(input);
  } else if (type === "itf14") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å€¤(0-9)ã®ã¿ã€‚ ãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã¾ãªã„13æ¡orãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã‚€14æ¡ã€‚
    const regexp = /^\d{13}$|^\d{14}$/;
    return regexp.test(input);
  } else if (type === "upca") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å€¤(0-9)ã®ã¿ã€‚ ãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã¾ãªã„11æ¡orãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã‚€12æ¡ã€‚
    const regexp = /^\d{11}$|^\d{12}$/;
    return regexp.test(input);
  } else if (type === "upce") {
    // æœ‰åŠ¹æ–‡å­—ã¯æ•°å€¤(0-9)ã®ã¿ã€‚ 1æ¡ç›®ã«æŒ‡å®šã§ãã‚‹æ•°å­—(ãƒŠãƒ³ãƒãƒ¼ã‚·ã‚¹ãƒ†ãƒ ã‚­ãƒ£ãƒ©ã‚¯ã‚¿)ã¯0ã®ã¿ã€‚
    // ãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã¾ãªã„7æ¡orãƒã‚§ãƒƒã‚¯ãƒ‡ã‚¸ãƒƒãƒˆã‚’å«ã‚€8æ¡ã€‚
    const regexp = /^0(\d{6}$|\d{7}$)/;
    return regexp.test(input);
  }
  return false;
};

export const createBarCode = async ({
  type,
  input,
  width,
  height,
}: {
  type: BarCodeType;
  input: string | null;
  width: number;
  height: number;
}): Promise<Buffer | null> => {
  if (input && validateBarcodeInput(type, input)) {
    const bwipjsArg = {
      bcid: type === "nw7" ? "rationalizedCodabar" : type,
      text: input,
      scale: 5,
      width,
      height,
      includetext: true,
    };
    const buffer = await bwipjs.toBuffer(bwipjsArg).catch(() => null);
    return buffer;
  } else {
    return null;
  }
};
